

/**
* @name Vec2
* @class A 2-dimensional vector.
* @description Creates a new Vec2 object
* @param {Number} [x] The x value. If x is an array of length 2, the array will be used to populate all components.
* @param {Number} [y] The y value
*/
export class Vec2 {
    data: Float32Array;

    constructor();
    constructor(x: number, y: number);
    constructor(x: [number, number]);
    constructor(x?, y?) {
        if (x && x.length === 2) {
            this.data = new Float32Array(x);
            return;
        }

        this.data = new Float32Array(2);

        this.data[0] = x || 0;
        this.data[1] = y || 0;
    }

    /**
     * @function
     * @name Vec2#add
     * @description Adds a 2-dimensional vector to another in place.
     * @param {Vec2} rhs The vector to add to the specified vector.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(10, 10);
     * var b = new Vec2(20, 20);
     *
     * a.add(b);
     *
     * // Should output [30, 30]
     * console.log("The result of the addition is: " + a.toString());
     */
    add(rhs: Vec2): this {
        var a = this.data,
            b = rhs.data;

        a[0] += b[0];
        a[1] += b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#add2
     * @description Adds two 2-dimensional vectors together and returns the result.
     * @param {Vec2} lhs The first vector operand for the addition.
     * @param {Vec2} rhs The second vector operand for the addition.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(10, 10);
     * var b = new Vec2(20, 20);
     * var r = new Vec2();
     *
     * r.add2(a, b);
     * // Should output [30, 30]
     *
     * console.log("The result of the addition is: " + r.toString());
     */
    add2(lhs: Vec2, rhs: Vec2): this {
        var a = lhs.data,
            b = rhs.data,
            r = this.data;

        r[0] = a[0] + b[0];
        r[1] = a[1] + b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#clone
     * @description Returns an identical copy of the specified 2-dimensional vector.
     * @returns {Vec2} A 2-dimensional vector containing the result of the cloning.
     * @example
     * var v = new Vec2(10, 20);
     * var vclone = v.clone();
     * console.log("The result of the cloning is: " + vclone.toString());
     */
    clone(): Vec2 {
        return new Vec2().copy(this);
    }

    /**
     * @function
     * @name Vec2#copy
     * @description Copied the contents of a source 2-dimensional vector to a destination 2-dimensional vector.
     * @param {Vec2} rhs A vector to copy to the specified vector.
     * @returns {Vec2} Self for chaining.
     * @example
     * var src = new Vec2(10, 20);
     * var dst = new Vec2();
     *
     * dst.copy(src);
     *
     * console.log("The two vectors are " + (dst.equals(src) ? "equal" : "different"));
     */
    copy(rhs: Vec2): this {
        var a = this.data,
            b = rhs.data;

        a[0] = b[0];
        a[1] = b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#dot
     * @description Returns the result of a dot product operation performed on the two specified 2-dimensional vectors.
     * @param {Vec2} rhs The second 2-dimensional vector operand of the dot product.
     * @returns {Number} The result of the dot product operation.
     * @example
     * var v1 = new Vec2(5, 10);
     * var v2 = new Vec2(10, 20);
     * var v1dotv2 = v1.dot(v2);
     * console.log("The result of the dot product is: " + v1dotv2);
     */
    dot(rhs: Vec2): number {
        var a = this.data,
            b = rhs.data;

        return a[0] * b[0] + a[1] * b[1];
    }

    /**
     * @function
     * @name Vec2#equals
     * @description Reports whether two vectors are equal.
     * @param {Vec2} rhs The vector to compare to the specified vector.
     * @returns {Boolean} true if the vectors are equal and false otherwise.
     * @example
     * var a = new Vec2(1, 2);
     * var b = new Vec2(4, 5);
     * console.log("The two vectors are " + (a.equals(b) ? "equal" : "different"));
     */
    equals(rhs: Vec2): boolean {
        var a = this.data,
            b = rhs.data;

        return a[0] === b[0] && a[1] === b[1];
    }

    /**
     * @function
     * @name Vec2#length
     * @description Returns the magnitude of the specified 2-dimensional vector.
     * @returns {Number} The magnitude of the specified 2-dimensional vector.
     * @example
     * var vec = new Vec2(3, 4);
     * var len = vec.length();
     * // Should output 5
     * console.log("The length of the vector is: " + len);
     */
    length(): number {
        var v = this.data;

        return Math.sqrt(v[0] * v[0] + v[1] * v[1]);
    }

    /**
     * @function
     * @name Vec2#lengthSq
     * @description Returns the magnitude squared of the specified 2-dimensional vector.
     * @returns {Number} The magnitude of the specified 2-dimensional vector.
     * @example
     * var vec = new Vec2(3, 4);
     * var len = vec.lengthSq();
     * // Should output 25
     * console.log("The length squared of the vector is: " + len);
     */
    lengthSq(): number {
        var v = this.data;

        return v[0] * v[0] + v[1] * v[1];
    }

    /**
     * @function
     * @name Vec2#lerp
     * @description Returns the result of a linear interpolation between two specified 2-dimensional vectors.
     * @param {Vec2} lhs The 2-dimensional to interpolate from.
     * @param {Vec2} rhs The 2-dimensional to interpolate to.
     * @param {Number} alpha The value controlling the point of interpolation. Between 0 and 1, the linear interpolant
     * will occur on a straight line between lhs and rhs. Outside of this range, the linear interpolant will occur on
     * a ray extrapolated from this line.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(0, 0);
     * var b = new Vec2(10, 10);
     * var r = new Vec2();
     *
     * r.lerp(a, b, 0);   // r is equal to a
     * r.lerp(a, b, 0.5); // r is 5, 5
     * r.lerp(a, b, 1);   // r is equal to b
     */
    lerp(lhs: Vec2, rhs: Vec2, alpha: number): this {
        var a = lhs.data,
            b = rhs.data,
            r = this.data;

        r[0] = a[0] + alpha * (b[0] - a[0]);
        r[1] = a[1] + alpha * (b[1] - a[1]);

        return this;
    }

    /**
     * @function
     * @name Vec2#mul
     * @description Multiplies a 2-dimensional vector to another in place.
     * @param {Vec2} rhs The 2-dimensional vector used as the second multiplicand of the operation.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(2, 3);
     * var b = new Vec2(4, 5);
     *
     * a.mul(b);
     *
     * // Should output 8, 15
     * console.log("The result of the multiplication is: " + a.toString());
     */
    mul(rhs: Vec2): this {
        var a = this.data,
            b = rhs.data;

        a[0] *= b[0];
        a[1] *= b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#mul2
     * @description Returns the result of multiplying the specified 2-dimensional vectors together.
     * @param {Vec2} lhs The 2-dimensional vector used as the first multiplicand of the operation.
     * @param {Vec2} rhs The 2-dimensional vector used as the second multiplicand of the operation.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(2, 3);
     * var b = new Vec2(4, 5);
     * var r = new Vec2();
     *
     * r.mul2(a, b);
     *
     * // Should output 8, 15
     * console.log("The result of the multiplication is: " + r.toString());
     */
    mul2(lhs: Vec2, rhs: Vec2): this {
        var a = lhs.data,
            b = rhs.data,
            r = this.data;

        r[0] = a[0] * b[0];
        r[1] = a[1] * b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#normalize
     * @description Returns the specified 2-dimensional vector copied and converted to a unit vector.
     * If the vector has a length of zero, the vector's elements will be set to zero.
     * @returns {Vec2} Self for chaining.
     * @example
     * var v = new Vec2(25, 0);
     *
     * v.normalize();
     *
     * // Should output 1, 0
     * console.log("The result of the vector normalization is: " + v.toString());
     */
    normalize(): this {
        var v = this.data;

        var lengthSq = v[0] * v[0] + v[1] * v[1];
        if (lengthSq > 0) {
            var invLength = 1 / Math.sqrt(lengthSq);
            v[0] *= invLength;
            v[1] *= invLength;
        }

        return this;
    }

    /**
     * @function
     * @name Vec2#scale
     * @description Scales each component of the specified 2-dimensional vector by the supplied
     * scalar value.
     * @param {Number} scalar The value by which each vector component is multiplied.
     * @returns {Vec2} Self for chaining.
     * @example
     * var v = new Vec2(2, 4);
     *
     * // Multiply by 2
     * v.scale(2);
     *
     * // Negate
     * v.scale(-1);
     *
     * // Divide by 2
     * v.scale(0.5);
     */
    scale(scalar: number): this {
        var v = this.data;

        v[0] *= scalar;
        v[1] *= scalar;

        return this;
    }

    /**
     * @function
     * @name Vec2#set
     * @description Sets the specified 2-dimensional vector to the supplied numerical values.
     * @param {Number} x The value to set on the first component of the vector.
     * @param {Number} y The value to set on the second component of the vector.
     * @example
     * var v = new Vec2();
     * v.set(5, 10);
     *
     * // Should output 5, 10
     * console.log("The result of the vector set is: " + v.toString());
     */
    set(x: number, y: number): this {
        var v = this.data;

        v[0] = x;
        v[1] = y;

        return this;
    }

    /**
     * @function
     * @name Vec2#sub
     * @description Subtracts a 2-dimensional vector from another in place.
     * @param {Vec2} rhs The vector to add to the specified vector.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(10, 10);
     * var b = new Vec2(20, 20);
     *
     * a.sub(b);
     *
     * // Should output [-10, -10]
     * console.log("The result of the addition is: " + a.toString());
     */
    sub(rhs: Vec2): this {
        var a = this.data,
            b = rhs.data;

        a[0] -= b[0];
        a[1] -= b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#sub2
     * @description Subtracts two 2-dimensional vectors from one another and returns the result.
     * @param {Vec2} lhs The first vector operand for the addition.
     * @param {Vec2} rhs The second vector operand for the addition.
     * @returns {Vec2} Self for chaining.
     * @example
     * var a = new Vec2(10, 10);
     * var b = new Vec2(20, 20);
     * var r = new Vec2();
     *
     * r.sub2(a, b);
     *
     * // Should output [-10, -10]
     * console.log("The result of the addition is: " + r.toString());
     */
    sub2(lhs: Vec2, rhs: Vec2): this {
        var a = lhs.data,
            b = rhs.data,
            r = this.data;

        r[0] = a[0] - b[0];
        r[1] = a[1] - b[1];

        return this;
    }

    /**
     * @function
     * @name Vec2#toString
     * @description Converts the vector to string form.
     * @returns {String} The vector in string form.
     * @example
     * var v = new Vec2(20, 10);
     * // Should output '[20, 10]'
     * console.log(v.toString());
     */
    toString(): string {
        return "[" + this.data[0] + ", " + this.data[1] + "]";
    }


    /**
     * @field
     * @type Number
     * @name Vec2#x
     * @description The first element of the vector.
     * @example
     * var vec = new Vec2(10, 20);
     *
     * // Get x
     * var x = vec.x;
     *
     * // Set x
     * vec.x = 0;
     */
    get x(): number {
        return this.data[0];
    };
    set x(value: number) {
        this.data[0] = value;
    }

    /**
     * @field
     * @type Number
     * @name Vec2#y
     * @description The second element of the vector.
     * @example
     * var vec = new Vec2(10, 20);
     *
     * // Get y
     * var y = vec.y;
     *
     * // Set y
     * vec.y = 0;
     */
    get y(): number {
        return this.data[1];
    };
    set y(value: number) {
        this.data[1] = value;
    }

    /**
     * @field
     * @static
     * @readonly
     * @type Vec2
     * @name Vec2.ONE
     * @description A constant vector set to [1, 1].
     */
    static readonly ONE: Vec2 = new Vec2(1, 1);

    /**
     * @field
     * @static
     * @readonly
     * @type Vec2
     * @name Vec2.RIGHT
     * @description A constant vector set to [1, 0].
     */
    static readonly RIGHT: Vec2 = new Vec2(1, 0);

    /**
     * @field
     * @static
     * @readonly
     * @type Vec2
     * @name Vec2.UP
     * @description A constant vector set to [0, 1].
     */
    static readonly UP: Vec2 = new Vec2(0, 1);

    /**
     * @field
     * @static
     * @readonly
     * @type Vec2
     * @name Vec2.ZERO
     * @description A constant vector set to [0, 0].
     */
    static readonly ZERO: Vec2 = new Vec2(0, 0);
}
